.container {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;      
  grid-template-areas: "header header header"
                      "deck-chooser deck-chooser deck-chooser"
                      ". table .";
}

.table{
  grid-area: table;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1.75fr 1.75fr 1.75fr 1.75fr; 
  column-gap: 10px;
  row-gap: 15px;
}

.table-item{
  background-color: grey;
  height: 275px;
  border-radius: 10px;
}

